import React, { Component } from "react";
import { DateRangePicker } from "@matharumanpreet00/react-daterange-picker";

import "./App.css";
import { DateRange } from "@matharumanpreet00/react-daterange-picker/build/types";

class App extends Component<{}, { open: boolean; dateRange: DateRange }> {
	state = {
		open: false,
		dateRange: {}
	};

	render() {
		return (
			<div className="App">
				<header className="App-header">
					<div>
						<a
							style={{ color: "black" }}
							onClick={() =>
								this.setState(prev => ({
									open: !prev.open
								}))
							}>
							toggle
						</a>
					</div>

					<pre style={{ color: "black" }}>
						{JSON.stringify(this.state.dateRange, null, 4)}
					</pre>

					<DateRangePicker
						title="my title"
						open={this.state.open}
						onChange={range => this.setState({ dateRange: range })}
					/>
				</header>
			</div>
		);
	}
}

export default App;
